body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.3px;
  color: #333;
}

/* primary-color: #006BB6; */

.top-part {
  flex: 0 0 auto;
  background-image: radial-gradient(circle at 100%, #3797db -90%, #006bb6 100%);
  padding-bottom: 156px;
}

.wrapper {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}

.site-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.site-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  padding: 32px 0;
}

.site-header .logo {
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 1px;
  color: #fff;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 8px;
  text-decoration: none;
}

.site-header .logo::before {
  content: "";
  width: 48px;
  height: 48px;
  display: inline-block;
  margin-right: 24px;
  background: url(/images/logo.svg) no-repeat center/48px;
  position: relative;
  top: -4px;
}

.site-header .logo span {
  font-size: 15px;
  line-height: 1;
  font-weight: 400;
  letter-spacing: 0.8px;
}

.site-header .help-nav {
  list-style: none;
  margin: 0;
  padding: 0 8px 0 0;
  display: flex;
}

.site-header .help-nav li a {
  color: #fff;
  text-decoration: none;
}

.site-header .help-nav li a:hover {
  opacity: 0.7;
}

.site-header .help-nav li + li {
  margin-left: 24px;
}

.main-search-form {
  position: relative;
  margin-bottom: 64px;
}

.main-search-form .form-control {
  width: 100%;
  height: 74px;
  color: #333;
  padding-left: 24px;
}

.main-search-form .form-control::placeholder {
  color: #3b4256;
  opacity: 0.4;
}

.main-search-form .btn-secondary {
  background-color: #e53935;
  border-color: #e53935;
  position: absolute;
  top: 8px;
  right: 8px;
  bottom: 8px;
  font-weight: 500;
  text-align: center;
  width: 184px;
  font-size: 15px;
}

.service-grid {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin-top: -90px;
}

.service-grid .item {
  margin-bottom: 26px;
  flex: 0 0 auto;
  background: #fff;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.012);
  border-radius: 4px;
  width: calc(50% - 12px);
  padding: 32px 48px 32px 32px;
  display: flex;
  align-items: flex-start;
  min-height: 190px;
  text-decoration: none;
}

.service-grid .item:hover {
  cursor: pointer;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.08);
}

.service-grid .item:nth-of-type(2n + 1) {
  margin-left: 0 !important;
}

.service-grid .item + .item {
  margin-left: 24px;
}

.bottom-part {
  flex: 1 1 auto;
  background-color: rgba(54, 54, 54, 0.04);
  padding-bottom: 48px;
}

.service-grid .item .icon {
  width: 46px;
  height: 46px;
  flex: 0 0 auto;
  margin-right: 24px;
  position: relative;
  top: -2px;
}

.service-grid .item-content {
  flex: 0 1 auto;
}

.service-grid .item-title {
  font-size: 22px;
  font-weight: 600;
  line-height: 24px;
  color: #333;
  margin-bottom: 16px;
  letter-spacing: -0.1px;
}

.service-grid .item-description {
  font-size: 14px;
  line-height: 20px;
  color: #3b4256;
  opacity: 0.4;
}

.gov-nav {
  padding: 16px 0;
  background-color: #00538c;
  font-weight: 500;
}

.gov-nav .wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);
}

.gov-nav .subnav {
  display: inline-flex;
  align-items: center;
}

.gov-nav .subnav::before {
  content: "";
  background: url(/images/menu-icon.svg) no-repeat center/18px 12px;
  display: inline-block;
  width: 18px;
  height: 18px;
  vertical-align: middle;
  margin-right: 12px;
}

.gov-nav .spacer {
  flex: 1 1 auto;
}

.gov-nav .auth {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  color: #fff;
  text-decoration: none;
}

.gov-nav .auth::after {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 4px 4px 0 4px;
  border-color: #fff transparent transparent transparent;
  margin-left: 8px;
}

.gov-nav .auth::before {
  content: "";
  background: url(/images/nav-auth-icon.svg) no-repeat center/20px;
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  margin-right: 10px;
}

.gov-nav .auth:hover {
  color: rgba(255, 255, 255, 0.8);
}

.gov-nav .translate-menu {
  display: inline-flex;
  margin-right: 24px;
  align-items: center;
  justify-content: flex-start;
}

.gov-nav .translate-menu::before {
  content: "";
  background: url(/images/translate-icon.svg) no-repeat center/20px;
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  margin-right: 10px;
}

.gov-nav .translate-menu::after {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 4px 4px 0 4px;
  border-color: rgba(255, 255, 255, 0.6) transparent transparent transparent;
  margin-left: 8px;
}

.promo-columns {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  color: rgba(255, 255, 255, 0.7);
}

.promo-columns .column {
  flex: 0 0 auto;
  width: calc(50% - 24px);
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.promo-columns .column:nth-of-type(2) .column-content::after {
  content: "";
  display: inline-block;
  height: 3px;
  width: 24px;
  background-color: #fff;
  box-shadow: 32px 0 0 rgba(255, 255, 255, 0.3), 64px 0 0 rgba(255, 255, 255, 0.3);
}

.promo-columns .login-column p {
  margin-bottom: 24px;
}

.promo-columns .login-column .first-btn {
  background-color: #fff;
  border-color: #fff;
  color: #333;
  padding: 11px 24px 13px;
  font-size: 15px;
}

.promo-columns .login-column .first-btn:hover {
  background-color: rgba(255, 255, 255, 0.8);
  border-color: rgba(255, 255, 255, 0.8);
}

.promo-columns .login-column .second-btn {
  margin-left: 16px;
  background-color: transparent;
  border-color: rgba(98, 176, 255, 0.5);
  color: #fff;
  font-size: 15px;
  padding: 11px 24px 13px;
}

.promo-columns .icon {
  width: 56px;
  height: 56px;
  border-radius: 16px;
  border: 2px solid #fff;
  margin-right: 32px;
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.promo-columns .column:nth-of-type(2n) .icon {
  background: url(/images/promo-icon1.png) no-repeat center/cover;
}

.promo-columns .column-content {
  flex: 0 1 auto;
}

.promo-columns h3 {
  font-size: 20px;
  line-height: 26px;
  color: #fff;
  margin-bottom: 8px;
  letter-spacing: -0.05px;
}

.promo-columns p {
  font-size: 13px;
  line-height: 16px;
}

/*

  User Cabinet

*/
.service-request-page .top-part,
.user-cabinet .top-part {
  padding-bottom: 0;
}

.service-request-page .top-part::after,
.user-cabinet .top-part::after {
  content: "";
  display: block;
  height: 4px;
  width: 100%;
  background: linear-gradient(to right, #e30016 0 40%, #009035 50% 100%);
}

.service-request-page .botton-part,
.user-cabinet .bottom-part {
  padding: 64px 0;
}

.user-cabinet .bottom-part h1 {
  font-size: 40px;
  line-height: 60px;
  font-weight: 300;
  margin-bottom: 32px;
}

.tab-pane {
  padding: 36px 0 48px;
}

.form-group {
  margin-bottom: 20px;
  padding-top: 12px;
}

.form-control {
  padding: 11px 16px;
  height: 48px;
}

.service-request-list .item {
  margin-bottom: 36px;
  line-height: 26px;
}

.service-request-list .item-status {
  font-size: 13px;
  margin-bottom: 16px;
  color: rgba(51, 51, 51, 0.4);
}

.service-request-list .status-accepted .item-name::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  background: url(/images/user-request-status1.svg) no-repeat center/15px 17px;
}

.service-request-list .status-complete .item-name::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  background: url(/images/user-request-status2.svg) no-repeat center/18px 14px;
  position: relative;
  top: 2px;
}

.service-request-list .status-wip .item-name::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  background: url(/images/user-request-status3.svg) no-repeat center/12px 20px;
}

.service-request-list .status-rejected .item-name::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  background: url(/images/user-request-status4.svg) no-repeat center/20px;
}

.service-request-list .item:last-of-type {
  margin-bottom: 0;
}

.service-request-list .item-name {
  font-size: 22px;
  font-weight: 300;
  letter-spacing: 0.5px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.service-request-page .bottom-part {
  padding: 48px 0 48px;
}

.service-request-page .bottom-part h1 {
  font-size: 40px;
  line-height: 60px;
  font-weight: 300;
  margin-bottom: 36px;
}

.service-request-page .bottom-part h3 {
  font-size: 30px;
  line-height: 36px;
}

.service-request-page .row + h3 {
  margin-top: 40px;
}

.form-actions {
  padding-top: 28px;
  display: flex;
  justify-content: flex-end;
}

.form-actions .btn {
  padding: 11px 24px 13px;
  font-size: 15px;
  background-color: #006bb6;
  border-color: #006bb6;
  color: #fff;
}

.drop-loader {
  position: relative;
  border: 1px dashed #ced4da;
  color: rgba(51, 51, 51, 0.4);
  min-height: 120px;
  padding: 14px 16px;
}

.drop-loader::before {
  display: inline-block;
  content: "";
  width: 16px;
  height: 16px;
  background: url("/images/clip-icon.svg") no-repeat center/contain;
  margin-right: 4px;
  vertical-align: middle;
  position: relative;
  top: -2px;
}

.drop-loader input[type="file"] {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.drop-loader label {
  color: #007bff;
  text-decoration: underline;
  cursor: pointer;
}

.drop-loader label:hover {
  text-decoration: none;
}

.mobile-menu-toggle {
  background: transparent url("/images/menu-icon2.svg") no-repeat center/28px 19px;
  padding: 0;
  margin-right: 8px;
  border: none;
  cursor: pointer;
  width: 36px;
  height: 36px;
}

.mobile-menu-toggle:focus {
  outline: none;
}

@media (max-width: 1101px) {
  .wrapper {
    padding: 0 16px;
  }
}

@media (max-width: 800px) {
  .service-grid {
    flex-direction: column;
  }

  .service-grid .item {
    width: 100%;
  }

  .service-grid .item:last-of-type {
    margin-bottom: 0;
  }

  .service-grid .item + .item {
    margin-left: 0;
  }

  .promo-columns {
    flex-direction: column;
    padding: 0 24px;
  }

  .promo-columns .column {
    width: 100%;
  }

  .promo-columns .column:first-of-type {
    margin-bottom: 26px;
  }

  .main-search-form {
    margin-bottom: 48px;
  }

  .top-part {
    padding-bottom: 130px;
  }

  .bottom-part {
    padding-bottom: 24px;
  }

  .site-header .help-nav {
    display: none;
  }

  .service-request-page .bottom-part h1,
  .user-cabinet .bottom-part h1 {
    font-size: 29px;
    line-height: 36px;
    margin-bottom: 24px;
  }

  .service-request-page .bottom-part h3 {
    font-size: 19px;
    line-height: 24px;
  }

  .service-request-page .row + h3 {
    margin-top: 24px;
  }

  .service-request-page .botton-part,
  .user-cabinet .bottom-part {
    padding: 40px 0 0;
  }
}

@media (min-width: 801px) {
  .site-header .mobile-menu-toggle {
    display: none;
  }
}
